            var self = this;
            var policies = data;
            var policyData = policies;
            Object.assign(policyData, {
                segments: [],
                selectedSegmentId: ''
            });
            policies.policy.forEach(function (block) {
                var segment = {
                    detail: block,
                    states: block.fsmDescription,
                    selected: false
                };

                policyData.segments.push(segment);
            });

            return policyData;
